body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
  color: white;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer; /* Устанавливаем курсор */
}

.progress-bar {
  position: absolute;
  top: 10px;
  width: 90%;
  display: flex;
  flex-direction: row; /* Явно указываем направление flex */
  justify-content: space-between;
  height: 2px; /* Ставим высоту для контейнера */
}

.progress-segment {
  flex: 1;
  height: 100%; /* Устанавливаем высоту в 100% для сегментов */
  background-color: rgba(255, 255, 255, 0.3);
  margin: 0 2px;
  position: relative;
  overflow: hidden;
}

.progress-segment .progress-inner {
  width: 100%;
  height: 100%; /* Устанавливаем высоту в 100% для внутреннего заполнения */
  background-color: white;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 3s linear;
}

.progress-segment.completed .progress-inner {
  transform: scaleX(1);
}

.progress-segment.active .progress-inner {
  transform: scaleX(1);
  animation: fill 3s linear forwards;
}

@keyframes fill {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

.slide {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

h1 {
  font-size: 3em;
  text-align: center;
}
